import { gsap } from "gsap";
import { useRef, useEffect } from "react";

function Navbar() {
// ------------------------TEST--------------------------------
  const timeline = gsap.timeline();
  const el = useRef();
  useEffect(() => {
    timeline.from(
      el.current,
      {
        opacity: 0,
        y: -200,
        immediateRender: false
      },
      "+=1"
    );
  }, [timeline]);

//********************************************************** */



  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark" aria-label="Fifth navbar example">
        <div className="container-fluid">
            <a className="navbar-brand" href="/" ref={el}>Logo</a>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbar-main" aria-controls="navbar-main" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbar-main">
                <ul className="navbar-nav mx-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                        <a className="nav-link active" aria-current="page" href="/">Inicio</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="/">Proyectos</a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" href="/">Sobre Mi</a>
                    </li>
                </ul>
                <button type="button" class="btn btn-primary">Contacto</button>
            </div>
        </div>
    </nav>
  )
}
export default Navbar;
